@import url(https://fonts.googleapis.com/css?family=Anton);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  padding: 0;
  margin: 0;
}

.App {
  text-align: center;
}

nav {
  display: flex;
  justify-content: space-around;
  align-items: center;
  min-height: 10vh;
  background: #347474;
  color: white;
  
}

.nav-link {
  text-decoration: none;
}

.nav-text {
  color: white;
}

.logo {
  color: white;
}

.cover {
  background-image: url(/static/media/city2.74c9657e.jpg);
  height: 100vh;
  background-size: cover;
  top: 0;
  position: relative;
}

.cover:before {
  background: #42b883;
  opacity: 0.5;
  content: "";
  left: 0; right: 0;
  top: 0; bottom: 0;
  position: absolute;
}

.cover-text {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-75%, -50%);
}

.cover-text p {
  color: white;
  text-align: left;
}

.cover-title {
  font-size: 6vw !important;
}

.cover-subTitle {
  font-size: 1.5vw;
  margin-top: 2vw;
}

.second {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-column-gap: 10px;
  background-size: cover;
  height: 100vh;
  padding-top: 15vh;
}

.second-text {
  grid-column: 1/2;
}

.second-text div {
  width: 35vw;
  text-align: left;
  transform: translate(45%, 30%);
}

.second-text div * {
  margin-bottom: 15px;
}

.second-image {
  grid-column: 2/3;
}

.second-image img{
  width: 550px;
}

.content {
  line-height: 35px;
  font-size: 25px;
}

.third {
  height: 100vh;
  display: grid;
  grid-template-rows: 1fr 3fr;
}

.third-lower {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  text-align: center;
}

.margin-auto {
  margin: auto;
}



